import React from 'react';
import { Page, Text, View, Document, StyleSheet, Image } from '@react-pdf/renderer';
import logoImg from '../../assets/images/reformat_logo.png';
import badgeImg from '../../assets/images/diploma/badge.png';
import centerImg from '../../assets/images/diploma/centerImage.png';

// Create styles
const styles = StyleSheet.create({
    page: {
        flexDirection: "column",
        backgroundColor: '#ffe264',
        padding: 15
    },
    header: {
        flexGrow: 2,
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
    },
    centerImage: {
        alignSelf: "center",
        flexGrow: 3
    },
    body: {
        flexGrow: 4,
        marginTop: 20,
        textAlign: 'center',
    },
    text: {
        paddingBottom: 10,
    },
    userNameText: {
        fontSize: 40,
        fontWeight: 900,
        textDecoration: 'underline'
    },
    beforeText: { fontSize: 28 },
    afterText: { fontSize: 15 },
});

// Create Document Component
const DiplomaDocument = ({ userName }) => (
    <Document >
        <Page orientation='landscape' style={styles.page} size="A4">
            <View style={styles.header}>
                <Image src={logoImg} alt="reformat logo" />
                <Image src={badgeImg} alt="diploma badge ilustration" />
            </View>
            <Image src={centerImg} style={styles.centerImage} alt="congrats image" />
            <View style={styles.body}>
                <Text style={{ ...styles.text, ...styles.beforeText }}>Diploma otorgat a</Text>
                <Text style={{ ...styles.text, ...styles.userNameText }}>{userName}</Text>
                <Text style={{ ...styles.text, ...styles.afterText, paddingTop: 25 }}>En reconeixement a l'esforç personal i resposta positiva alhora de treballar en equip.</Text>
                <Text style={{ ...styles.text, ...styles.afterText }}>Gràcies per participar en aquesta missió, tindrem en compte la vostra  dedicació per a futures expedicions.</Text>
            </View>

            {/* <View style={styles.view}>
                <Image style={styles.imageLogo} src={logoImg} alt="images" />
            </View> */}
        </Page>
    </Document>
    // <Document>
    //     <Page size="A4" orientation='landscape' style={styles.page}>
    //         <View style={styles.section}>
    //             <Image src={kidImg} style={{
    //                 objectFit: 'cover',
    //             }} />
    //             {/* <Text>Felicitats {userName}!</Text> */}
    //         </View>
    //     </Page>
    // </Document>
);

export default DiplomaDocument;